#landing-area {
	width: 100vw;
	height: 90vh;
	
	position: relative;
  }
  
  #box-left {
	width: calc(50% - 10vh);
	padding: 5px 11vh 5px 5px;
	text-align: left;
	display: inline-block;
	height: 20%;
	box-sizing: border-box;
  /* font-weight: 200; */
  line-height: 200%;
  }
  
  #box-right {
	width: calc(50% + 10vh);
	clip-path: polygon(10vh 0, 100% 0, 100% 100%, 0 100%);
	padding: 5px 5px 5px 11vh;
	text-align: center;
	background-image: url('../../img/profile.png');
	background-size: cover;
	background-position: center center;
	display: inline-block;
	height: 100%;
	position: absolute;
	box-sizing: border-box;
	

  }
  
  #middle-text {
	color: #e2fcef;
	font-size:x-large;
	text-size-adjust: 2;
	height: 200px;
	width: 400px;
	position: fixed;
	top: 30%;
	left: 25%;
	margin-top: -100px;
	margin-left: -200px;
  }
  
  #middle-text p{
	color: #e2fcef;
	font-size: large;
	text-size-adjust: 2;
	height: 200px;
	width: 500px;
	position: relative;
	top: 50%;
	left: 50%;
	margin-top: -100px;
	margin-left: -200px;
  }