#contact-page {
	width: 50%;
	margin-left: 20%;
	margin-top: 2%;
	padding: 2rem!important;
	border-radius: 20px;
	align-items:center;
	align-content: center;
	/* display: inline-block; */
  }

  #contact-note{
	font-size: large;
	text-size-adjust: 2;
	/* height: 200px; */
	width: 80%;
	/* position: fixed; */
  }



#contact-details {
	/* max-width: 20%!important; */
	width: 25%;
	margin: 1.1rem;
	position: inherit;
	padding: 2rem!important;
	border-radius: 20px;
	box-shadow: 0 0 30px 0 rgb(0 0 0 / 10%);
	display: inline-block;
  }

  #contact-icon{
	color: white;
	font-size: 40px;
	text-align: center;
	
  }
  
  #contact-icon span,p,a{
	color: whitesmoke;
	font-size: 15px;
	/* width: 100%; */
  }
  