.resume{
  float:left;
  align-content: center;
  width: 100%;
  /* background-color: aquamarine; */
}
.techstack{
  width: 30%;
  float:left;
  align-content: center;
}

#page{
  width: 100%;
  margin-top: 15%;
  
}

#devlist{
  margin-top: 10px;
  margin-left: 25%;
  width: 100%;
  height: 20%;
  text-align: justify;
  align-items:center;
  border-radius: 2px;
  float:left;
  overflow-wrap: break-word;
}

#devicon{
  color: white;
  font-size: 40px;
  width: 70px;
  margin-top: 10px;
  margin-bottom: 10px;
  height: 70px;
  text-align: center;
  width: 10%;
  float: left;
  
}

#devicon span{
  color: whitesmoke;
  font-size: 15px;
  width: auto;
  overflow-wrap: break-word;
  display: block;
  font-weight: lighter;
}


.history{
  width: 70%;
  float: right;
  margin-top: 2%;

}

.historyblock{
  background-color: #1f3f65;
}

.historyblock p{
  /* background-color: #ffffff; */
  font-weight: lighter;
  margin-top: -2px;
}


.company{
  color: #E3ECF2; 
  text-align: right;
  margin-top: 0%;
  margin-bottom: 0%;

}

.company-sub{
  text-align: right;
  margin-top: 0%;
  margin-bottom: 0%;
  font-weight: lighter;
